.sec1 {
  min-height: 100vh;
  width: 100%;
  display: flex;
  background-color: #0b0c19;
}
.sec1 {
  padding-top: 50px;
}
.sec1 img {
  height: 600px;
  width: 450px;
}

.detail-main {
  padding: 100px;
}

.detail-main p {
  font-size: 20px;
  padding: 10px;
  color: white;
}
.detail-main h2 {
  font-size: 50px;
  color: white;
  padding: 10px;
}
.detail-main h4 {
  color: white;
}
.defh {
  display: flex;
  flex-direction: row;
  padding: 80px;
  padding-bottom: 0;
}
.defh-item {
  color: white;
  border-bottom: 1px solid #d3323f;
  border-right: 1px solid #d3323f;
  padding: 20px;
  width: 400px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hide-button {
  display: flex;
  justify-content: center;
}

.pcdetail-hide {
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.pcdetail-hide1 {
  margin: 40px;
}
.pcdetail-hide2 {
  margin: 17px;
  display: flex;
  flex-direction: row;
}
.pcdetail-para {
  width: 150px;
}

.defh2 {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin-bottom: 100px;
}
.defh2 p {
  color: white;
  min-height: 200px;
  width: 200px;
  padding: 70px;
  border-right: 1px solid #d3323f;
}

.detail-box-left {
  height: 500px;
}
.detail-box-right {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px;
}
.detail-box2-left {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 500px;
}
.detail-box2-right {
  height: 500px;
}
.detail-box-text {
  padding: 100px;
  color: white;
}

@media (max-width: 650px) {
  .sec1 {
    padding-top: 20px;
  }
  .sec1 img {
    margin-left: 20px;
    height: 350px;
    width: 260px;
  }
  .detail-main {
    padding: 15px;
  }
  .detail-main p {
    font-size: 18px;
    padding: 10px;
  }
  .detail-main h2 {
    font-size: 30px;
    color: white;
    padding: 10px;
  }
  .defh {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 30px;
  }
  .pcdetail-hide {
    flex-direction: column;
    padding: 20px;
    align-items: center;
  }
  .pcdetail-hide1 {
    margin: 0px;
  }
  .pcdetail-hide2 {
    margin: 5px;
    display: flex;
    flex-direction: row;
  }
  .pcdetail-hide2 p {
    font-size: 13px;
  }
  .pcdetail-para {
    width: 100px;
  }
  .defh-item {
    border-right: none;
    padding: 20px;
    width: 250px;
    height: 250px;
    display: flex;
  }
  .defh-item h3 {
    font-size: 20px;
  }
  .defh-item p {
    font-size: 15px;
    margin: 10px;
  }
  .defh2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 20px;
  }
  .defh2 p {
    color: white;
    margin: 10px;
    min-height: 70px;
    width: 80px;
    padding: 10px;
    border-bottom: 1px solid #d3323f;
    border-right: none;
  }
  .defh3 {
    margin-top: 20px;
    width: 400px;
    height: 250px;
    border-right: 1px solid #d3323f;
  }
  .detail-box-left {
    height: 500px;
    width: 100%;
  }
  .detail-box-right {
    height: 300px;
    width: 100%;
  }
  .detail-box2-left {
    padding: 30px;
    margin-left: 20px;
    height: 400px;
    width: 100%;
  }
  .detail-box2-right {
    height: 400px;
    width: 100%;
  }
  .detail-box-text {
    margin-top: 50px;
    padding: 20px;
  }
  .detail-box-text h1 {
    font-size: 35px;
  }
  .detail-box-text p {
    margin-top: 30px;
    letter-spacing: 1px;
  }
}
