@import url("https://fonts.googleapis.com/css2?family=Playfair+Display+SC&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap");

.marketing {
  font-family: "Playfair Display SC", serif;
}

.back {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back {
  background-image: url(../../assets/landing3.png);
  background-size: cover;
  background-position: top center;
  position: center;
}
.back .box {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  justify-content: 50px;
}
.back::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.7;
  z-index: -1;
}
.back h1 {
  display: block;
  width: fit-content;
  font-size: 4rem;
  position: relative;
  color: transparent;
  animation: text_reveal 0.5s ease forwards;
  animation-delay: 1s;
}
.back h1:nth-child(1) {
  animation-delay: 1s;
}
.back h1:nth-child(2) {
  animation-delay: 1.5s;
}
.back h1 span {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: crimson;
  animation: text_reveal_box 1s ease;
  animation-delay: 0.5s;
}
#back h1:nth-child(1) span {
  animation-delay: 0.5s;
}
#back h1:nth-child(2) span {
  animation-delay: 1.5s;
}

.meterial {
  display: flex;
  flex-direction: column;
  padding: 5rem;
}
.meterial h2 {
  color: white;
  font-size: 25px;
  font-weight: 500;
}
.meterial p {
  color: white;
  font-size: 15px;
  font-weight: 300;
}
.meterial span {
  color: #dd343d;
  font-size: 40px;
}

.sec2 {
  background-color: black;
  min-height: 60vh;
  width: 100%;
}

.sec-5-left {
  background-image: url(../../assets/rohit8.png);
  background-size: cover;
  background-position: top center;
  position: center;
  min-height: 60vh;
}
.sec-6-right {
  background-image: url(../../assets/rohit9.png);
  background-size: cover;
  background-position: top center;
  position: center;
  min-height: 60vh;
}
.stage2 {
  min-height: 60vh;
  width: 100%;
  background-color: black;
  display: flex;
  justify-content: center;
  text-align: center;
}
.midtag {
  margin-top: 100px;
  margin-bottom: 30px;
  height: 300px;
  width: 700px;
  display: flex;
  flex-direction: column;
  color: white;
}
.midbutton button {
  background: none;
  color: #ccc;
  width: 170px;
  height: 50px;
  border: 1px solid #dd343d;
  font-size: 18px;
  border-radius: 4px;
  transition: 0.6s;
  overflow: hidden;
}
.midbutton button:focus {
  outline: none;
}
.midbutton button:hover {
  background: #dd343d;
  cursor: pointer;
}
.midbutton button:hover:before {
  transform: translateX(300px) skewX(-15deg);
  opacity: 0.6;
  transition: 0.7s;
}
.midbutton button:hover:after {
  transform: translateX(300px) skewX(-15deg);
  opacity: 1;
  transition: 0.7s;
}

.stage3 {
  background-color: black;
  background-size: cover;
  padding: 60px 0;
}
.inner-width {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
  overflow: hidden;
}
.section-title {
  text-align: center;
  color: #ddd;
  text-transform: uppercase;
  font-size: 30px;
}
.border {
  width: 160px;
  height: 2px;
  background: #dd343d;
  margin: 40px auto;
}
.service-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.service-box {
  max-width: 33.33%;
  padding: 10px;
  text-align: center;
  color: #ddd;
  cursor: pointer;
}
.service-icon {
  display: inline-block;
  width: 62px;
  height: 62px;
  border: 3px solid #dd343d;
  transform: rotate(45deg);
  margin-bottom: 30px;
  margin-top: 16px;
  transition: 0.3s linear;
  overflow: hidden;
}
.service-logo {
  transform: rotate(-45deg);
}
.service-box:hover .service-icon {
  background: #dd343d;
  color: #ddd;
}
.service-title {
  font-size: 25px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.service-desc {
  font-size: 17px;
}

.landinglast {
  height: 550px;
}

@keyframes text_reveal_box {
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes text_reveal {
  100% {
    color: white;
  }
}
@media (max-width: 700px) {
  .back .box {
    padding: 10px;
  }
  .landinglast {
    display: none;
  }
  .back h1 {
    font-size: 3rem;
  }
  .service-box {
    max-width: 100%;
  }
  .service-icon {
    width: 65px;
    height: 66px;
  }
  .meterial {
    padding: 2rem;
  }
  .meterial h2 {
    font-size: 30px;
    font-weight: 500;
  }
  .meterial p {
    font-size: 14px;
    font-weight: 200;
  }
  .midtag {
    margin-top: 70px;
    margin-bottom: 10px;
    height: 550px;
    width: 700px;
    display: flex;
    flex-direction: column;
    color: white;
  }
  .bestcol2 img {
    display: block;
    height: 500px;
    width: 300px;
  }
}
